<template>
	<div class="play-full-footer">
		<div class="progress">
			<span class="currentTime">{{currentTime|formatduration}}</span>
			<input type="range" min="0" :max="duration" step="1" @input="getValue" :value="currentTime">
			<span class="jd" :style="{width:(currentTime/duration)*100+'%'}">
				<span class="btn"></span>
			</span>
			<span class="duration">{{duration|formatduration}}</span>
		</div>
		<br>
		<br>
		<div class="switch">
			<div class="left" @click="$emit('playLast')"></div>
			<div class="mid" :class="{paused:paused}" @click="$emit('togglePlayState')"></div>
			<div class="right" @click="$emit('nextSong')"></div>
		</div>
	</div>
</template>
<script>
	export default {
		props: ["currentTime", "duration", "paused"],
		data: function() {
			return {};
		},
		methods: {
			getValue: function(e) {
				// console.log(e.target.value);
				this.$emit('update:currentTime', e.target.value)
			},
			nextsong: function() {

			},
			// PadZero: function(str) {
			// 	//补零
			// 	return new RegExp(/^\d$/g).test(str) ? `0${str}` : str;
			// }
		},
		filters: {
			// formatcurrentTime: function(time) {

			// },
			formatduration: function(time) {
				time = parseInt(time);
				let mins = parseInt(time/ 60);
				mins=mins <10? "0" + mins : mins;
				let seconds = parseInt(time % 60);
				seconds=seconds <10? "0" + seconds : seconds;
				// result = "mins : seconds";
				return mins+':'+seconds;
			}
		}
	};
</script>

<style lang="less">
	.play-full-footer {
		position: absolute;
		bottom: 50px;
		left: 0;
		width: 100%;
	}

	.progress {
		width: 80%;
		height: 6px;
		background-color: #9f9ec2;
		position: relative;
		top: 0;
		left: 0;
		margin: 0 auto;
		border-radius: 3px;

		input {
			width: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 99;
			opacity: 0;
		}

		.jd {
			display: block;
			position: absolute;
			position: relative;
			top: 0;
			left: 0;
			width: 0%;
			height: 100%;
			background: linear-gradient(to right, #CD96CD, #FFE4E1);
			border-radius: 3px;
			.btn{
				position: absolute;
				background: url(../imgs/蜘蛛侠.png) no-repeat;
				display: inline-block;
				width: 25px;
				height: 25px;
				background-size: 100%;
				right: -20px;
				top: -9px;
			}
		}

		.currentTime {
			position: absolute;
			display: inline-block;
			color: wheat;
			left: -32px;
			top: -5px;
			font-size: 10px;
		}

		.duration {
			position: absolute;
			display: inline-block;
			color: wheat;
			top: -5px;
			right: -32px;
			font-size: 10px;
		}
	}

	.switch {
		height: 50px;

		.left {
			&::before {
				content: "";
				display: block;
				background: url(../imgs/_上一首.png) no-repeat;
				height: 50px;
				width: 50px;
				transform: scale(0.7);
				background-size: 100%;
			}
		}

		.mid {
			&.paused {
				&::before {
					content: "";
					display: block;
					background: url(../imgs/播放.png) no-repeat;
					height: 50px;
					width: 50px;
					transform: scale(0.9);
					background-size: 100%;
				}
			}
		}

		.right {
			float: right;
			margin-right: 60px;

			&::before {
				content: "";
				display: block;
				background: url(../imgs/_下一首.png) no-repeat;
				height: 50px;
				width: 50px;
				transform: scale(0.7);
				background-size: 100%;
			}
		}
	}

	.switch div {
		display: inline-block;
		margin-left: 50px;
	}

	.mid {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 45px;
		left: 100px;

		&::before {
			content: "";
			display: block;
			background: url(../imgs/暂停.png) no-repeat;
			height: 50px;
			width: 50px;
			transform: scale(0.95);
			background-size: 100%;
		}
	}
</style>
